<template>
  <div>
    <main>
      <div class="title">
        <h1>Poubelle</h1>
      </div>
      <div class="trash">
        <h2><button @click="$router.push('/dashboard')">Retour</button> Éléments supprimés</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="trashLoading"></BarLoader>
        <Element v-for="element in getTrash" v-bind="element" :key="element.id" actions="true"></Element>
        <h2 v-if="getTrash.length == 0">La corbeille est vide</h2>
      </div>
    </main>
  </div>
</template>
<script>
import Element from '@/components/card/element.vue'
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'home',
  components: {
    Element
  },
  created () {
    if(this.jwt) this.fetchTrash()
  },
  computed: {
      ...mapGetters('trash', [
        'getTrash',
        'trashLoading'
      ]),
      ...mapGetters('jwt', [
        'jwt'
      ])
  },
  methods: {
    ...mapActions('trash', [
      `fetchTrash`,
      `restoreElement`,
      `deleteElement`
    ]),
    ...mapActions('alert', [
      'newAlert'
    ])
  }
}
</script>
<style scoped>
button {
    outline: none;
    transition: all .3s;
    user-select: none;
    cursor: pointer;
    display: inline-block;
    border-radius: 7px;
    border: none;
    font-size: 15px;
    background: var(--b3);
    border: 1px solid var(--b3);
    color: var(--b8);
    margin: 10px 15px 0 0;
    padding: 10px 16px;
}

button.outline {
    background: none;
    color: var(--b3);
}
button:hover { 
    border: 1px solid var(--b2);
    background: var(--b2);
}
button.outline:hover { 
    border: 1px solid var(--b3);
    background: var(--b3);
    color: var(--b8);
}
  
</style>